import {firestore} from 'firebase';
import * as ExcelJS from "exceljs";
import {CellHyperlinkValue, Style, Worksheet} from "exceljs";
import {saveAs} from "file-saver";
import {UserTypes} from "../enums";
import {
    C_USERS,
    D_APPLICANT_MEDIA,
    D_EDUCATION_BACKGROUND,
    D_EMERGENCY_CONTACT,
    D_HEALTH_CONDITION,
    D_PERSONAL_DETAILS,
    D_PERSONAL_STATEMENT
} from "../constants";
import {IDocUser} from "../constants/interfaces";
import {HOST_NAME} from "../constants/api";

class Workbook {
    private static instance: Workbook;

    private constructor() {
    }

    static getInstance(): Workbook {
        if (!Workbook.instance) {
            Workbook.instance = new Workbook();
        }

        return Workbook.instance;
    }

    async createAndSave(data: IDocUser[]) {
        // Create Workbook
        let workbook = new ExcelJS.Workbook();
        workbook.creator = 'UM iCamp 2020 Web System';
        workbook.created = new Date();
        workbook.modified = new Date();

        // Setup style for Header
        const headerStyle: Partial<Style> = {
            font: {
                bold: true,
            },
            alignment: {
                horizontal: "center",
            },
            border: {
                bottom: {
                    style: "double"
                }
            }
        }

        let wsGeneralInfo: Worksheet = this.createGeneralInfo(workbook, headerStyle);
        let wsPersonalDetails: Worksheet = this.createPersonalDetails(workbook, headerStyle);
        let wsEducationBackground: Worksheet = this.createEducationBackground(workbook, headerStyle);
        let wsHealthCondition: Worksheet = this.createHealthCondition(workbook, headerStyle);
        let wsPersonalStatement: Worksheet = this.createPersonalStatement(workbook, headerStyle);
        let wsEmergencyContact: Worksheet = this.createEmergencyContact(workbook, headerStyle);
        let wsApplicantMedia: Worksheet = this.createApplicantMedia(workbook, headerStyle);

        // Add Row to workbook
        data
            .sort((a: IDocUser, b: IDocUser) =>
                b.countApplicationForm - a.countApplicationForm ||
                Number(b.applicationForm?.configData.isSubmitted) - Number(a.applicationForm?.configData.isSubmitted))
            .filter(u => u.role === UserTypes.USER)
            .forEach((user: IDocUser, index: number) => {
                this.addRowToGeneralInfo(wsGeneralInfo, user, index, data.length);
                this.addRowToPersonalDetails(wsPersonalDetails, user, index, data.length);
                this.addRowToEducationBackground(wsEducationBackground, user, index);
                this.addRowToHealthCondition(wsHealthCondition, user, index);
                this.addRowToPersonalStatement(wsPersonalStatement, user, index);
                this.addRowToEmergencyContact(wsEmergencyContact, user, index);
                this.addRowToApplicantMedia(wsApplicantMedia, user, index);
            });

        // Create buffer and meta data to save as a file
        const buffer = await workbook.xlsx.writeBuffer();
        const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
        const fileExtension = '.xlsx';
        const blob = new Blob([buffer], {type: fileType});
        saveAs(blob, `Applicants UMiCamp 2020 - ${new Date().toISOString()}` + fileExtension);
    }

    private createGeneralInfo(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(C_USERS);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Id', key: 'id', width: 20},
            {header: 'Email', key: 'email', width: 25},
            {header: 'Created at', key: 'createdAt', width: 15},
            {header: 'Status', key: 'status', width: 12},
            {header: 'Personal Details', key: 'personalDetails', width: 20},
            {header: 'Education Background', key: 'educationBackground', width: 20},
            {header: 'Health Condition', key: 'healthCondition', width: 20},
            {header: 'Personal Statement', key: 'personalStatement', width: 20},
            {header: 'Emergency Contact', key: 'emergencyContact', width: 20},
            {header: 'Application Media', key: 'applicationMedia', width: 20},
        ];
        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getColumn('D').style = {alignment: {horizontal: "center"}};
        ws.getColumn('F').style = {alignment: {horizontal: "center"}};
        ws.getColumn('G').style = {alignment: {horizontal: "center"}};
        ws.getColumn('H').style = {alignment: {horizontal: "center"}};
        ws.getColumn('I').style = {alignment: {horizontal: "center"}};
        ws.getColumn('J').style = {alignment: {horizontal: "center"}};
        ws.getColumn('K').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;
        ws.getCell('H1').style = style;
        ws.getCell('I1').style = style;
        ws.getCell('J1').style = style;
        ws.getCell('K1').style = style;

        return ws;
    }

    private addRowToGeneralInfo(ws: Worksheet, user: IDocUser, index: number, totalRow: number): void {
        let state = "Registered";
        if (user.countApplicationForm > 0 && !user?.applicationForm?.configData?.isSubmitted)
            state = "Filling up";
        else if (user.applicationForm?.configData.isSubmitted)
            state = "Submitted";

        ws.addRow([
            (index + 1),
            user.uid,
            user.email?.toLowerCase(),
            user.createdAt,
            state,
            // `=HYPERLINK("#"&CELL("address"; INDEX('${D_PERSONAL_DETAILS}'!B2:B${totalRow};MATCH(B${index + 2};'${D_PERSONAL_DETAILS}'!B2:B${totalRow};0)));"Open")`,
            // `=HYPERLINK("#"&CELL("address"; INDEX('${D_EDUCATION_BACKGROUND}'!B2:B${totalRow};MATCH(B${index + 2};'${D_EDUCATION_BACKGROUND}'!B2:B${totalRow};0)));"Open")`,
            // `=HYPERLINK("#"&CELL("address"; INDEX('${D_HEALTH_CONDITION}'!B2:B${totalRow};MATCH(B${index + 2};'${D_HEALTH_CONDITION}'!B2:B${totalRow};0)));"Open")`,
        ]);

        ws.getCell(`F${index + 2}`).value = {
            hyperlink: `#'${D_PERSONAL_DETAILS}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;

        ws.getCell(`G${index + 2}`).value = {
            hyperlink: `#'${D_EDUCATION_BACKGROUND}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;

        ws.getCell(`H${index + 2}`).value = {
            hyperlink: `#'${D_HEALTH_CONDITION}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;

        ws.getCell(`I${index + 2}`).value = {
            hyperlink: `#'${D_PERSONAL_STATEMENT}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;

        ws.getCell(`J${index + 2}`).value = {
            hyperlink: `#'${D_EMERGENCY_CONTACT}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;

        ws.getCell(`K${index + 2}`).value = {
            hyperlink: `#'${D_APPLICANT_MEDIA}'!B${(index + 2)}`,
            text: "Open",
        } as CellHyperlinkValue;
    }

    private createPersonalDetails(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_PERSONAL_DETAILS);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Full name', key: 'fullName', width: 30},
            {header: 'Known as', key: 'knownAs', width: 20},
            {header: 'Address', key: 'address', width: 30},
            {header: 'City', key: 'city', width: 15},
            {header: 'State', key: 'state', width: 20},
            {header: 'Postcode', key: 'postcode', width: 10},
            {header: 'Country', key: 'Country', width: 20},
            {header: 'Mobile', key: 'mobile', width: 20},
            {header: 'Phone', key: 'phone', width: 20},
            {header: 'WhatsApp', key: 'wa', width: 20},
            {header: 'Facebook', key: 'fb', width: 15},
            {header: 'Instagram', key: 'ig', width: 15},
            {header: 'T-Shirt', key: 'tShirt', width: 10},
            {header: 'Date of Birth', key: 'tShirt', width: 15},
            {header: 'Gender', key: 'gender', width: 10},
            {header: 'Marital status', key: 'maritalStatus', width: 15},
            {header: 'Religion', key: 'religion', width: 10},
            {header: 'Passport No.', key: 'passportNo', width: 15},
            {header: 'Expiry Date', key: 'expiryDate', width: 15},
            {header: 'Nationality', key: 'nationality', width: 20},
        ];
        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;
        ws.getCell('H1').style = style;
        ws.getCell('I1').style = style;
        ws.getCell('J1').style = style;
        ws.getCell('K1').style = style;
        ws.getCell('L1').style = style;
        ws.getCell('M1').style = style;
        ws.getCell('N1').style = style;
        ws.getCell('O1').style = style;
        ws.getCell('P1').style = style;
        ws.getCell('Q1').style = style;
        ws.getCell('R1').style = style;
        ws.getCell('S1').style = style;
        ws.getCell('T1').style = style;
        ws.getCell('U1').style = style;
        ws.getCell('V1').style = style;

        return ws;
    }

    private addRowToPersonalDetails(ws: Worksheet, user: IDocUser, index: number, totalRow: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
            user.applicationForm?.personalDetails.fullName,
            user.applicationForm?.personalDetails.nickName,
            user.applicationForm?.personalDetails.address,
            user.applicationForm?.personalDetails.city,
            user.applicationForm?.personalDetails.province,
            user.applicationForm?.personalDetails.postcode,
            user.applicationForm?.personalDetails.country,
            user.applicationForm?.personalDetails.mobilePhone,
            user.applicationForm?.personalDetails.phone,
            user.applicationForm?.personalDetails.waNumber,
            user.applicationForm?.personalDetails.facebook,
            user.applicationForm?.personalDetails.instagram,
            user.applicationForm?.personalDetails.tShirtSize,
            // @ts-ignore
            (user.applicationForm?.personalDetails.dateOfBirth as firestore.Timestamp)?.toDate(),
            user.applicationForm?.personalDetails.sex,
            user.applicationForm?.personalDetails.maritalStatus,
            user.applicationForm?.personalDetails.religion,
            user.applicationForm?.personalDetails.passportNumber,
            // @ts-ignore
            (user.applicationForm?.personalDetails.expiryDate as firestore.Timestamp)?.toDate(),
            user.applicationForm?.personalDetails.nationality,
        ]);

        ws.getCell(`B${index + 2}`).value = {
            hyperlink: `#${C_USERS}!F${(index + 2)}`,
            text: user.email?.toLowerCase(),
        } as CellHyperlinkValue;
    }

    private createEducationBackground(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_EDUCATION_BACKGROUND);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Latest Degree', key: 'latestDegree', width: 15},
            {header: 'Home University', key: 'homeUniversity', width: 30},
            {header: 'Faculty', key: 'faculty', width: 20},
            {header: 'Department', key: 'department', width: 20},
            {header: 'Country', key: 'country', width: 20},
        ];
        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;

        return ws;
    }

    private addRowToEducationBackground(ws: Worksheet, user: IDocUser, index: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
            user.applicationForm?.educationBackground?.latestDegree,
            user.applicationForm?.educationBackground?.homeUniversity,
            user.applicationForm?.educationBackground?.faculty,
            user.applicationForm?.educationBackground?.department,
            user.applicationForm?.educationBackground?.country,
        ]);
        ws.getCell(`B${index + 2}`).value = {
            hyperlink: `#'${C_USERS}'!G${(index + 2)}`,
            text: user.email?.toLowerCase(),
        } as CellHyperlinkValue;
    }

    private createHealthCondition(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_HEALTH_CONDITION);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Dietary Restriction ', key: 'dietaryRestriction', width: 30},
            {header: 'Dietary Detail', key: 'dietaryDetail', width: 30},
            {header: 'Disability', key: 'disability', width: 20},
            {header: 'Disability Detail', key: 'disabilityDetail', width: 30},
            {header: 'Medical Condition', key: 'medicalCondition', width: 20},
            {header: 'Special Requirements', key: 'specialRequirements', width: 20},
        ];
        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;
        ws.getCell('H1').style = style;

        return ws;
    }

    private addRowToHealthCondition(ws: Worksheet, user: IDocUser, index: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
            user.applicationForm?.healthCondition?.dietaryRestriction?.toString().replace(',', ', '),
            user.applicationForm?.healthCondition?.dietaryDetail,
            user.applicationForm?.healthCondition?.disability,
            user.applicationForm?.healthCondition?.disabilityDetail,
            user.applicationForm?.healthCondition?.medicalCondition,
            user.applicationForm?.healthCondition?.specialRequirements,
        ]);
        ws.getCell(`B${index + 2}`).value = {
            hyperlink: `#'${C_USERS}'!H${(index + 2)}`,
            text: user.email?.toLowerCase(),
        } as CellHyperlinkValue;
    }

    private createPersonalStatement(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_PERSONAL_STATEMENT);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Motivation', key: 'motivation', width: 65},
            {header: 'Personal Experiences', key: 'personalExperiences', width: 65},
            {header: 'Specialities Describe', key: 'specialitiesDescribe', width: 65},
            {header: 'After Program', key: 'afterProgram', width: 65},
            {header: 'Information Sources', key: 'informationSources', width: 30},
            {header: 'Additional', key: 'additionalInformationSources', width: 30},
        ];

        ws.getColumn('A').style = {alignment: {vertical: "top", horizontal: "center"}};
        ws.getColumn('B').style = {alignment: {vertical: "top", horizontal: "left"}};
        ws.getColumn('C').style = {alignment: {wrapText: true, vertical: "top", horizontal: "left"}};
        ws.getColumn('D').style = {alignment: {wrapText: true, vertical: "top", horizontal: "left"}};
        ws.getColumn('E').style = {alignment: {wrapText: true, vertical: "top", horizontal: "left"}};
        ws.getColumn('F').style = {alignment: {wrapText: true, vertical: "top", horizontal: "left"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;
        ws.getCell('H1').style = style;

        return ws;
    }

    private addRowToPersonalStatement(ws: Worksheet, user: IDocUser, index: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
            user.applicationForm?.personalStatement?.motivation,
            user.applicationForm?.personalStatement?.personalExperiences,
            user.applicationForm?.personalStatement?.specialitiesDescribe,
            user.applicationForm?.personalStatement?.afterProgram,
            user.applicationForm?.personalStatement?.informationSources?.toString().replace(',', ', '),
            user.applicationForm?.personalStatement?.additionalInformationSources,
        ]);
        ws.getRow(index + 2).height = 50;
        ws.getCell(`B${index + 2}`).value = {
            hyperlink: `#'${C_USERS}'!I${(index + 2)}`,
            text: user.email?.toLowerCase(),
        } as CellHyperlinkValue;
    }

    private createEmergencyContact(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_EMERGENCY_CONTACT);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Full name', key: 'fullName', width: 30},
            {header: 'Relationship', key: 'relationship', width: 20},
            {header: 'Email', key: 'email', width: 25},
            {header: 'Phone', key: 'phone', width: 15},
            {header: 'Address', key: 'address', width: 20},
            {header: 'City', key: 'city', width: 10},
            {header: 'Province', key: 'province', width: 20},
            {header: 'Country', key: 'country', width: 20},
            {header: 'Postcode', key: 'postcode', width: 20},
        ];
        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;
        ws.getCell('G1').style = style;
        ws.getCell('H1').style = style;
        ws.getCell('I1').style = style;
        ws.getCell('J1').style = style;
        ws.getCell('K1').style = style;

        return ws;
    }

    private addRowToEmergencyContact(ws: Worksheet, user: IDocUser, index: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
            user.applicationForm?.emergencyContact?.fullName,
            user.applicationForm?.emergencyContact?.relationship,
            user.applicationForm?.emergencyContact?.email,
            user.applicationForm?.emergencyContact?.phone,
            user.applicationForm?.emergencyContact?.address,
            user.applicationForm?.emergencyContact?.city,
            user.applicationForm?.emergencyContact?.province,
            user.applicationForm?.emergencyContact?.country,
            user.applicationForm?.emergencyContact?.postcode,
        ]);

        ws.getCell(`B${index + 2}`).value = {
            hyperlink: `#${C_USERS}!J${(index + 2)}`,
            text: user.email?.toLowerCase(),
        } as CellHyperlinkValue;
    }

    private createApplicantMedia(workbook: ExcelJS.Workbook, style: Partial<Style>): Worksheet {
        let ws: Worksheet = workbook.addWorksheet(D_APPLICANT_MEDIA);
        ws.columns = [
            {header: 'No', key: 'no', width: 5},
            {header: 'Email', key: 'id', width: 35},
            {header: 'Passport Photo', key: 'passportPhoto', width: 20},
            {header: 'Passport Scan', key: 'passportScan', width: 20},
            {header: 'Medical Certificate', key: 'medicalCertificate', width: 20},
            {header: 'Declaration Letter', key: 'declarationLetter', width: 20},
        ];

        ws.getColumn('A').style = {alignment: {horizontal: "center"}};
        ws.getColumn('C').style = {alignment: {horizontal: "center"}};
        ws.getColumn('D').style = {alignment: {horizontal: "center"}};
        ws.getColumn('E').style = {alignment: {horizontal: "center"}};
        ws.getColumn('F').style = {alignment: {horizontal: "center"}};
        ws.getCell('A1').style = style;
        ws.getCell('B1').style = style;
        ws.getCell('C1').style = style;
        ws.getCell('D1').style = style;
        ws.getCell('E1').style = style;
        ws.getCell('F1').style = style;

        return ws;
    }

    private addRowToApplicantMedia(ws: Worksheet, user: IDocUser, index: number): void {
        ws.addRow([
            (index + 1),
            user.uid,
        ]);
        ws.getCell(`B${index + 2}`).value = {
            text: user.email?.toLowerCase(),
            hyperlink: `#'${C_USERS}'!K${(index + 2)}`,
        } as CellHyperlinkValue;

        ws.getCell(`C${index + 2}`).value = {
            text: 'Open',
            hyperlink: HOST_NAME + user.applicationForm?.applicantMedia?.passportPhoto,
        } as CellHyperlinkValue;
        ws.getCell(`D${index + 2}`).value = {
            text: 'Open',
            hyperlink: HOST_NAME + user.applicationForm?.applicantMedia?.passportScan,
        } as CellHyperlinkValue;
        ws.getCell(`E${index + 2}`).value = {
            text: 'Open',
            hyperlink: HOST_NAME + user.applicationForm?.applicantMedia?.medicalCertificate,
        } as CellHyperlinkValue;
        ws.getCell(`F${index + 2}`).value = {
            text: 'Open',
            hyperlink: HOST_NAME + user.applicationForm?.applicantMedia?.declarationLetter,
        } as CellHyperlinkValue;
    }

}

export default Workbook;